import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { HomeComponent } from './home/home.component';

const routes: Routes = [
  { path: '', redirectTo: 'home', pathMatch: 'full' },//default path
  { path: 'home', component: HomeComponent },
  { path: 'user', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\users\\user.module')['UserModule']);  });}) },
  { path: 'course', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\courses\\course.module')['CourseModule']);  });}) },
  { path: 'survey', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\survey\\survey.module')['SurveyModule']);  });}) },
  { path: 'feedback', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\feedback\\feedback.module')['FeedbackModule']);  });}) },
  { path: 'gallery', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\gallery\\gallery.module')['GalleryModule']);  });}) },
  { path: 'profile', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\profile\\profile.module')['ProfileModule']);  });}) },
//  { path: 'slides', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\slides\\slide.module')['SlideModule']);  });}) },
//  { path: '**', component: PageNotFoundComponent }
//  { path: '**', component: HomeComponent }//otherwise
	{path: '*path', component: HomeComponent}
];

export const routing: ModuleWithProviders = RouterModule.forRoot(routes);



